export default {
	onShareAppMessage(res) {
		return {
			title: '购物先领券，一年省一半',
			path: '/pages/index/index'
		}
	},

	data() {
		return {
			logoMao: '../../static/img/mao.png',
			logoTaoBao: '../../static/img/taobao.png',
			isEnable:'否',
			tab: [{
					name: '热销',
					position: 4,
					total: 0,
					data: []
				},
				{
					name: '最新',
					position: 0,
					total: 0,
					data: []
				},
				{
					name: '超低价',
					position: 1,
					total: 0,
					data: []
				},
				{
					name: '高佣金',
					position: 5,
					total: 0,
					data: []
				},
				{
					name: '大额券',
					position: 3,
					total: 0,
					data: []
				},
			],
			category: [
				'全部',
				'女装',
				'男装',
				'内衣',
				'美妆',
				'配饰',
				'鞋品',
				'箱包',
				'儿童',
				'母婴',
				'居家',
				'美食',
				'数码',
				'家电',
				'其他',
				'车品',
				'文体',
				'宠物',
			],
			TabCur: 0,
			TabCurs: 0,
			scrollLeft: 0,
			scrollLefts: 0,
			couponlist: [],
			page: 1,
			cid: 0,
			type: 1,
			isInvitation: 0,
			sort: 4,
			genderKey: "gender",
			loadingType: 0,
			scrollTop: false,
			contentText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			}
		}
	},
	onLoad: function(e) {
		let a = this.$queue.getData("isEnable")
		if (a) {
			this.isEnable = a;
		}
		if (e.title && e.type) {
			this.type = e.type;
			uni.setNavigationBarTitle({
				title: e.title
			});
			uni.showLoading({
				title: '加载中...'
			});
			this.loadCouponList();
			let userId = this.$queue.getData("userId");
			if (userId) {
				this.$Request.getT("/user/" + userId).then(res => {
					if (res.status === 0) {
						this.$queue.setData("image_url", res.data.image_url);
						this.$queue.setData("mobile", res.data.phone);
						this.isInvitation = res.data.isInvitation;
						this.$queue.setData("isInvitation", res.data.isInvitation);
						this.$queue.setData("relation", res.data.invitation);
						this.$queue.setData("grade", res.data.grade);
						this.$queue.setData("nickName", res.data.nickName);
						this.$queue.setData("relation_id", res.data.relationId);
						this.$queue.setData("gender", parseInt(res.data.gender));
					}
				});
			}
		} else {
			uni.navigateBack()
		}
	},
	onPageScroll: function(e) {
		this.scrollTop = e.scrollTop > 200;
	},
	methods: {
		addPublisher() {
			if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) { //判断iPhone|iPad|iPod|iOS
				uni.showModal({
					title: "下载提示",
					content: '苹果APP正在上架中请先关注公众号使用哦',
					showCancel: true,
					success: (e) => {
						if (e.confirm) {
							uni.navigateTo({
								url: '/pages/member/erweima'
							});
						}
					}
				});
			} else if (/(Android)/i.test(navigator.userAgent)) { //判断Android
				window.location.assign("https://fir.vip/d/5yilc")
			} else { //pc
				window.location.assign(this.$queue.publicYuMing())
			}
		},
		searchSelect(e) {
			this.TabCurs = e.currentTarget.dataset.id;
			this.sort = this.tab[e.currentTarget.dataset.id].position;
			this.scrollLefts = (e.currentTarget.dataset.id - 1) * 60;
			this.page = 1;
			uni.showLoading({
				title: '加载中...'
			});






			this.loadCouponList("Refresh");
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.cid = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60;
			this.page = 1;
			uni.showLoading({
				title: '加载中...'
			});






			this.loadCouponList("Refresh");
		},
		topScrollTap: function() {
			uni.pageScrollTo({
				scrollTop: 0,
				duration: 300
			});
		},

		loadCouponList: function(type) {
			console.log("--------list----11-----");
			console.log(this.cid);
			console.log(this.page);
			console.log(this.sort);
			console.log("--------list----22-----");
			this.loadingType = 1;
			this.$Request.get(this.$Api.common.couponlist, {
				Cat: this.cid,
				StartPrice: "1",
				EndPrice:"10",
				PageNo: this.page
			}).then(res => {
				this.loadingType = 0;
				if (res.Code === 1) {
					if (this.page === 1) {
						this.couponlist = [];
					}
					res.Message.forEach(d => {
						d.tkmoneys = (d.tkmoney * 0.7).toFixed(2);
						let grade = this.$queue.getData("grade");
						if (grade) {
							d.tkmoney = (d.tkmoney * parseFloat(grade)).toFixed(2);
						} else {
							d.tkmoney = (d.tkmoney * 0.3).toFixed(2);
						}
						d.itemsale = d.itemsale > 10000 ? '已售 ' + (d.itemsale / 10000).toFixed(1) + '万' : '已售 ' + d.itemsale;
						this.couponlist.push(d);
					})

				} else {
					this.loadingType = 2;
				}
				uni.hideLoading();
				if (type === "Refresh") {
					uni.stopPullDownRefresh(); // 停止刷新
				}
			})
		},
		toGoodsInfo: function(e) {
			let relation_id = this.$queue.getData("relation_id");
			if (relation_id) {
				uni.navigateTo({
					url: "/pages/detail/goodsinfo?data=" + encodeURIComponent(JSON.stringify(e)) + "&relation_id=" + relation_id,
				})
			} else {
				uni.navigateTo({
					url: "/pages/detail/goodsinfo?data=" + encodeURIComponent(JSON.stringify(e)),
				})
			}
		},
	},

	onReady: function() {
		this.loadCouponList();
	},
	onReachBottom: function() {
		this.page = this.page + 1;
		this.loadCouponList();
	},
	onPullDownRefresh: function() {
		this.page = 1;
		this.loadCouponList("Refresh");
	}
}
